<template>
  <div name="PostArticle" class="PostArticle-wrap">
    <el-row style="margin: 10px 0 50px 50px">
      <font class="title"></font>
    </el-row>

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <div class="forms">
        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font></font>
          <div
            style="display: flex; width: 400px; justify-content: space-between"
          >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="1"
              border
              ></el-radio
            >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="2"
              border
              ></el-radio
            >
          </div>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>* </span></font>
          <el-form-item prop="author_name">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.author_name"
              placeholder="()"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font></font>
          <el-form-item prop="author_head_photo">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.author_head_photo"
              placeholder="~f12"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>* </span></font>
          <el-form-item prop="article_link">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_link"
              placeholder="()"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>* </span></font>
          <el-form-item prop="article_title">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_title"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>* </span></font>
          <el-cascader
            style="width: 400px; height: 70px"
            v-model="ruleForm.article_major"
            placeholder=""
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            :value="value"
          ></el-cascader>
        </el-row>

        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font></font>
          <el-select
            v-model="ruleForm.article_labels"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="()"
            style="width: 400px"
          >
            <el-option
              v-for="item in optionsSubject"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 900px">
          <font><span>* </span></font>
          <el-form-item prop="article_introduce">
            <el-input
              style="width: 700px"
              class="public-uncheck"
              v-model="ruleForm.article_introduce"
              placeholder="()"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row>
          <el-col :span="0.5">
            <font><span>* </span></font>
          </el-col>
        </el-row>

        <el-row type="flex" justify="space-between">
          <el-form-item prop="article_details">
            <mavon-editor
              style="min-height: 500px; max-height: 800px; min-width: 1100px"
              ref="md"
              @change="change"
              v-model="ruleForm.article_details"
            />
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="end" style="margin: 30px 60px 0 0">
          <div class="submit" @click="submitArticle()">
            <font></font>
          </div>
        </el-row>
      </div>
    </el-form>
  </div>
</template>

<script>
import { postArticleListAPI } from "@/api/homeAPI/articleslist";
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "PostArticle",
  data() {
    return {
      optionsSubject: [],
      value: [],
      options: [
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
