            },
          ],
        },
        {
          value: "",
          label: "",
        },
      ],
      ruleForm: {
        // 
        article_status: "1", //
        article_link: "",
        article_title: "",
        article_major: "",
        article_labels: [],
        article_introduce: "",
        article_details: "",

        // 
        author_id: 0, //
        author_status: 0, //
        author_head_photo: "",
        author_name: "",
        article_num: 0,

        //
        collection_num: 0,
        comment_num: 0,
        like_num: 0,
        share_num: 0,
      },
      html: "", // markdownhtml
      rules: {
        article_title: [
          { required: true, message: "", trigger: "blur" },
        ],
        article_details: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    // 
    change(_, render) {
      // render  markdown [html]
      this.html = render;
    },
    submitArticle() {
      if (
        (this.ruleForm.article_status == "1" &&
          this.ruleForm.author_name &&
          this.ruleForm.article_link &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details) ||
        (this.ruleForm.article_status == "2" &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details)
      ) {
        // ruleForm
        this.ruleForm.article_details = this.html;
        // 
        if (
          !this.ruleForm.author_head_photo &&
          this.ruleForm.article_status == "1"
        ) {
          this.ruleForm.author_head_photo = "https://dummyimage.com/400X400";
        }
        if (this.ruleForm.article_status == "2") {
          this.ruleForm.author_status = 1;
          this.ruleForm.author_id = JSON.parse(
            localStorage.getItem("user_info")
          ).id;
        }
        // 
        let article_labels = [];
        if (localStorage.getItem("article_labels")) {
          for (
            var i = 0;
            i < JSON.parse(localStorage.getItem("article_labels")).length;
            i++
          ) {
            article_labels.push(
              JSON.parse(localStorage.getItem("article_labels"))[i]
            );
          }
        }
        let same = [];
        article_labels.forEach((item1) => {
          this.ruleForm.article_labels.forEach((item2) => {
            if (item1.value == item2) {
              console.log(item2);
              same.push(item2);
            }
          });
        });
        if (same.length != 0) {
          // 
          let result = this.ruleForm.article_labels.filter((item1) => {
            return same.every((item2) => {
              return item1 != item2;
            });
          });
          // console.log("", result);
          result.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          console.log(article_labels);
        } else {
          // console.log("", this.ruleForm.article_labels);
          this.ruleForm.article_labels.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          console.log(article_labels);
        }
        localStorage.setItem("article_labels", JSON.stringify(article_labels));

        postArticleListAPI(this.ruleForm).then((res) => {});
        // 
        this.ruleForm = {
          article_title: "",
          article_major: "",
          article_details: "",
          article_num: 0,
          author_head_photo: "",
          author_name: "",
          collection_num: 0,
          comment_num: 0,
          like_num: 0,
          share_num: 0,
        };
        // 
        this.$notify({
          title: "",
          message: "",
          type: "success",
        });
      } else {
        // 
        this.$notify({
          title: "",
          message: "",
          type: "error",
        });
      }
    },
  },
  mounted() {
    if (localStorage.getItem("article_labels")) {
      for (
        var i = 0;
        i < JSON.parse(localStorage.getItem("article_labels")).length;
        i++
      ) {
        this.optionsSubject.push(
          JSON.parse(localStorage.getItem("article_labels"))[i]
        );
      }
    }
  },
  components: { mavonEditor },
};
</script>
<style scoped>
.PostArticle-wrap {
  margin-bottom: 30px;
  padding: 20px;
  width: 1300px;
  border-radius: 5px;
  background: #ffffff;
}
.title {
  font-family: SourceHanSansCN-Bold;
  font-size: 36px;
  font-weight: bold;
  color: #3d3d3d;
}
.forms {
  margin: 0 0 0 100px;
}
.forms > * {
  margin: 20px 0 20px 0;
}
.forms > * font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
}
.forms > * font span {
  color: #ff8200;
}
.submit {
  width: 150px;
  height: 60px;
  border-radius: 30px;
  background: #76fff5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}
.submit font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  color: #3d3d3d;
  transition: all 0.5s;
}
.submit:hover {
  background-color: #00ead8;
}
.submit:hover font {
  color: #ffffff;
}
</style>